<template>
	<div class="l-bg">
		<div class="l-container">
			<div class="l-srction-tips">重置</div>
			<div class="l-resetInfo">
				<input class="l-formSubmitBtn l-resetSubmitBtn" type="button" @click="resetData" value="重置转发状态">
				<input type="button" class="l-formSubmitBtn l-resetSubmitBtn" @click="resetShare" value="重置朋友圈转发状态">
			</div>
			<!--TODO 公告-->
			<!------------20190321-新增小优公告--------------->
			<div class="l-srction-tips">小优公告</div>
			<div class="l-shopInfo">
				<div class="l-formBox notice_main">
					<div class="l-formRowView notice_row" v-for="(item,index) in noticeList">
						<span class="notice_row_tit">公告{{index+1}}</span>
						<input type="text" v-model="item.noticeContent" placeholder="请输入公告文字" class="l-formInput" maxlength="21" ref="count" />
						<input type="text" v-model="item.shopId" placeholder="请输入店铺ID" class="l-formInput" style="width: 100px;" maxlength="5" />
						<span class="notice_del notice_btn" @click="deleteNotice(index,item.noticeId)">删除</span>
						<span class="notice_sav notice_btn" @click="saveNotice(item)" v-text="saveText"></span>
					</div>

					<div class="new_notice">
						<input type="button" @click="newNotice" value="新增公告" class="l-formButton" />
						<!--<input type="button" @click="addNotice" value="+提交公告" class="l-formButton" />-->
					</div>

				</div>
			</div>

			<div class="l-srction-tips">优惠券兑换统计</div>
			<div class="l-resetSearch">
				<input class="l-formMiddleSmallerInput" @click="showCouponStartTime" readonly="readonly" v-model="couponStartTime">
				<input class="l-formMiddleSmallerInput" @click="showCouponEndTime" readonly="readonly" v-model="couponEndTime">
				<date-time ref="couponStartTime" type="date" @confirm="setCouponStartTime"></date-time>
				<date-time ref="couponEndTime" type="date" @confirm="setCouponEndTime"></date-time>
				<input class="l-searchButton" type="button" @click="couponSearch" value="点击搜索">
			</div>

			<div class="l-resetInfo">
				<table border="1" class="table">
					<tr class="tableTitle">
						<th>店铺编号</th>
						<th>店铺名称</th>
						<th>兑换状态</th>
						<th>兑换数量</th>
					</tr>
					<tr v-for="item in count" :key="item.shopId">
						<td>{{item.shopId}}</td>
						<td>{{item.shopName}}</td>
						<td>{{item.couponState==0?"已兑换未使用":"已兑换已使用"}}</td>
						<td>{{item.couponCount}}</td>
					</tr>
					<tr v-if="noCount">
						<td colspan="4">暂无记录</td>
					</tr>
				</table>
			</div>
			<div class="l-srction-tips">点击量统计</div>
			<div class="l-resetSearch">
				<input class="l-formMiddleSmallerInput" @click="showClickStartTime" readonly="readonly" v-model="clickStartTime">
				<input class="l-formMiddleSmallerInput" @click="showClickEndTime" readonly="readonly" v-model="clickEndTime">
				<date-time ref="clickStartTime" type="date" @confirm="setClickStartTime"></date-time>
				<date-time ref="clickEndTime" type="date" @confirm="setClickEndTime"></date-time>
				<input class="l-searchButton" type="button" @click="clickSearch" value="点击搜索">
			</div>
			<div class="l-resetInfo">
				<table border="1" class="table">
					<tr class="tableTitle">
						<th>店铺编号</th>
						<th>店铺名称</th>
						<th>详情页浏览总量</th>
						<th>转发按钮点击量</th>
						<th>分享卡点击量</th>
					</tr>
					<tr v-for="item in clickCount" :key="item.shopId">
						<td>{{item.shopId}}</td>
						<td>{{item.shopName}}</td>
						<td>{{item.scanTimes}}</td>
						<td>{{item.buttonCount}}</td>
						<td>{{item.shareCount}}</td>
					</tr>
					<tr v-if="noClickCount">
						<td colspan="5">暂无记录</td>
					</tr>
				</table>
			</div>
			<div class="l-resetInfo">
				<div>
					<p class="l-mark" v-for="(item,index) in markList" :key="index">{{item}}</p>
				</div>
			</div>
		</div>
		<!--提示框-->
		<div v-if="mask" class="l-mask">
			<div class="l-mask-model">
				<div class="l-mask-modelChild">
					<h3>提示</h3>
					<p>{{masktext}}</p>
					<div class="l-btnBox">
					 	<button class="yes" @click="hidemask">确定</button>
						
					</div>
				</div>
			</div>
		</div>
		<div v-if="mask2" class="l-mask">
			<div class="l-mask-model">
				<div class="l-mask-modelChild">
					<h3>提示</h3>
					<p>{{masktxt}}</p>
					<div class="l-btnBox">
						<button class="yes" @click="hidemask2">确定</button>
						<button class="yes no" @click="hidemask2">取消</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import DateTime from "vue-date-time-m";
	import axios from "axios";
	export default {
		data() {
			return {
				mask: false,
				mask2:false,
				masktext: "",
				couponStartTime: this.getNowFormatDate(),
				couponEndTime: this.getNowFormatDate(),
				clickStartTime: this.getNowFormatDate(),
				clickEndTime: this.getNowFormatDate(),
				count: "",
				noCount: false,
				clickCount: "",
				noClickCount: false,
				markList: ["备注：", "1、“详情页浏览总量”自发布之日起计算，与选择日期无关", "2、“分享卡点击量”是指用户分享店铺信息给微信好友或者微信群后，分享信息被成功点击的次数，同一家店铺同一日同一个用户的有效点击次数最多为1，也就是说同一天同一个用户点击了同一个分享信息多次，但这家店铺的“分享卡点击量”只会加1。"],
				noticeList: [{ //TODO 共告data
					noticeContent: "",
					shopId: "",
				}, ],
				item: [{
					noticeContent: '',
					shopId: ''
				}, ],
				saveText:"保存"

			}
		},
		components: {
			DateTime
		},
		methods: {
			// 显示弹出框
			showmask: function(masktext) {
				this.mask = true;
				this.masktext = masktext;
			},
			showmask2: function(masktxt) {
				this.mask2 = true;
				this.masktxt = masktxt;
			},
			// 隐藏弹出框
			hidemask: function() {
				this.mask = false;
			},
			hidemask2: function() {
				this.mask2 = false;
			},
			 
			// 重置转发状态
			resetData: function() {
				var that = this;
				axios
					.get("http://139.196.73.183:8088/pointrecord/resetstatus")
					.then(function(response) {
						// console.log(response);
						if(response.data.errorCode == "0000") {
							that.showmask("重置成功！");
						} else {
							that.showmask(response.data.errorMsg);
						}
					})
					.catch(function(error) {
						console.log(error);
					});
			},
			//重置朋友圈转发状态
			resetShare: function() {
				var that = this;
				axios.get("https://www.95cfun.cn/custShare/updShareStatue?shareStyle=05").then(function(response) {
					if(response.data.errorCode == "0000") {
						// console.log(response);
						that.showmask("重置成功！");
					} else {
						that.showmask(response.data.errorCode);
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			//TODO 新增公告
			newNotice:function() {
//				this.noticeL = [];
//				var that = this;
//				axios({
//					headers: {
//						'Authorization': that.token
//					},
//					url: 'http://139.196.73.183:8088/notice',
//					method: "POST",
//					//  			data:that.noticeList
//				}).then(function(response) {
//					if(response.data.errorCode == '0000') {
//						that.showmask("保存成功！")
//						setTimeout(function() {
//							window.location.reload()
//						}, 500)
//					} else {
//						that.showmask(response.data.errorMsg)
//					}
//				}).catch(function(error) {
//					console.log(error);
//				});
				this.noticeList.push({
					noticeContent: "",
					shopId: "",
				});
			},
			// 删除公告
			deleteNotice: function(index, noticeId) {				
				var that=this;
				axios({
					url: 'http://139.196.73.183:8088/notice/'+noticeId,
					method: "DELETE",
				}).then(function(response) {
					if(response.data.errorCode == '0000'){
						that.noticeList.splice(index,1);
					} else {
						that.showmask("删除失败！")
					}
				}).catch(function(error) {
					console.log(error);
					that.showmask("系统异常！")
					
				});

			},
			//更新公告
			saveNotice: function(notice) {
				//	公告内容	
				var noticeL =notice;
				var that = this;
				var methods="PUT";
				if(notice.noticeContent==""){
					that.showmask("公告内容不能为空")
				}else{
					if(notice.noticeId==undefined){
					methods="POST";
					noticeL=new Array(notice);
				}			
				axios({
						url: 'http://139.196.73.183:8088/notice',
						method: methods,
						data:noticeL
					}).then(function(response) {
						if(response.data.errorCode == '0000') {
							that.showmask("保存成功！")
						} else{
							that.showmask(response.data.errorMsg)
						}
					}).catch(function(error) {
						console.log(error);
					});
				}		
			},
			
			
			//TODO 限制公告长度

			// 优惠卷开始日期
			//  展示日期组件
			showCouponStartTime() {
				this.$refs.couponStartTime.show();
			},
			// 日期组件回调
			setCouponStartTime(val) {
				this.couponStartTime = val.replace(new RegExp("/", "g"), "-");
			},
			// 优惠卷结束日期
			showCouponEndTime() {
				this.$refs.couponEndTime.show();
			},
			setCouponEndTime(val) {
				this.couponEndTime = val.replace(new RegExp("/", "g"), "-");
			},
			// 点击量开始日期
			showClickStartTime() {
				this.$refs.clickStartTime.show();
			},
			setClickStartTime(val) {
				this.clickStartTime = val.replace(new RegExp("/", "g"), "-");
			},
			// 点击量结束日期
			showClickEndTime() {
				this.$refs.clickEndTime.show();
			},
			setClickEndTime(val) {
				this.clickEndTime = val.replace(new RegExp("/", "g"), "-");
			},
			//优惠卷搜索
			couponSearch: function() {
				var that = this;
				axios
					.get(
						"http://139.196.73.183:8088/statistics?startTime=" +
						this.couponStartTime +
						"&endTime=" +
						this.couponEndTime
					)
					.then(function(response) {
						// console.log(response);
						if(response.data.data.length > 0) {
							that.count = response.data.data;
						} else {
							that.noCount = true;
						}
					})
					.catch(function(error) {
						console.log(error);
					});
			},

			//点击量搜索
			clickSearch: function() {
				var that = this;
				axios
					.get(
						"http://139.196.73.183:8088/statistics/clickcount?startTime=" +
						this.clickStartTime +
						"&endTime=" +
						this.clickEndTime
					)
					.then(function(response) {
						// console.log(response);
						if(response.data.data.length > 0) {
							that.clickCount = response.data.data;
						} else {
							that.noClickCount = true;
						}
					})
					.catch(function(error) {
						console.log(error);
					});
			}
		},
//		 watch:{
//				item:{
//					handler:function(){
//						var _this = this;
//						var _contSum = 1;
//						_this.$refs.count.setAttribute("maxlength",_contSum);
//					},
//					deep:true
//				}
//			 },
		mounted: function() {
			var that = this;
			//查询优惠券兑换统计
			this.couponSearch();
			//点击量统计
			this.clickSearch();
			/*------------加载公告列表Start----------*/
			axios({
				//		headers: {'Authorization': that.token},
				url: 'http://139.196.73.183:8088/notice',
				method: "get",
			}).then(function(response) {
				if(response.data.errorCode == '0000') {
					that.noticeList = response.data.data;
				} else {
					that.showmask(response.data.errorMsg)
				}
			}).catch(function(error) {
				console.log(error);
			});
			/*------------加载公告列表End----------*/

		}
	};
</script>
<style>
	.date-time-item {
		width: 500px;
		height: 300px;
		background: #eeeeee;
	}
	
	.date-time-input {
		width: 100px;
		height: 40px;
		background: pink;
		margin: 5px;
	}
	
	.l-mark {
		margin-bottom: 8px;
	}
</style>


